body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav-bar {
  display: flex;
  flex-direction: row;
  padding: 10px 50px;
  flex-wrap: wrap;
  background: burlywood;
  font-size: 1.8em;
}

.home-button {
  width: 43px;
  height: 45px;
  margin: auto 30px auto 0;
}

.home-button > img {
  max-width: 100%;
}

.upcoming-button {
  color: #000;
}
.not-found {
  text-align: center;
}
.button {
  padding: 10px 20px;
  border: 0;
  border-radius: 5px;
  color: #fff;
  background-color: #4e1d04;
  font-size: 1rem;
  cursor: pointer;
  margin: 5px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.button:hover {
  background-color: #3d1703;
}

.pushed {
  color: #4e1d04;
  background-color: #fff;
  border-color: #4e1d04;
  border: 1px;
}

.pushed:hover {
  background-color: #eee;
}

.cinema-button {
  padding: 10px 20px;
  border: 0;
  border-radius: 5px;
  color: #fff;
  background-color: #0ed108;
  font-size: 1rem;
  cursor: pointer;
  margin: 5px;
}

.schedule {
  background-color: #29a525;
}

.schedule:hover {
  background-color: #247c21;
}

.cancel {
  background-color: rgb(250, 0, 0);
  margin: auto;
  margin-top: 10px;
}

.cancel:hover {
  background-color: #e40505;
}
.movieSpace {
  max-width: 220px;
  min-width: 180px;
  max-height: 600px;
  border-radius: 4px;
  box-shadow: 0 0 10px #333;
  background-color: beige;
  display: flex;
  flex-direction: column;
  text-align: center;
  overflow: hidden;
  flex: 2 1;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.movieSpace > a {
  height: 100%;
}

.movieSpace > * {
  color: black;
}

.movieSpace:hover {
  box-shadow: 0 0 40px #333;
  -webkit-transform: scale(1.1, 1.1);
          transform: scale(1.1, 1.1);
}

.movieSpace > a > img {
  max-width: 100%;
}

.rating {
  display: flex;
  flex-direction: row;
}

.rating > p {
  margin: auto 0 auto auto;
}

.logo {
  max-width: 90px;
  max-height: 75px;
  margin: auto auto auto 0;
}

.title {
  font-size: 1.1em;
}

.release-date {
  font-weight: bold;
}
.bullseye {
  display: flex;
  height: 70vh;
}

.container {
  display: flex;
  margin: auto;
}

.dash {
  margin: 0 15px;
  width: 35px;
  height: 15px;
  border-radius: 8px;
  background: rgb(179, 161, 161);
  box-shadow: 0 0 10px 0 rgb(226, 204, 226);
}

.uno {
  margin-right: -18px;
  -webkit-transform-origin: center left;
          transform-origin: center left;
  -webkit-animation: spin 3s linear infinite;
          animation: spin 3s linear infinite;
}

.dos {
  -webkit-transform-origin: center right;
          transform-origin: center right;
  -webkit-animation: spin2 3s linear infinite;
          animation: spin2 3s linear infinite;
  -webkit-animation-delay: .2s;
          animation-delay: .2s;
}

.tres {
  -webkit-transform-origin: center right;
          transform-origin: center right;
  -webkit-animation: spin3 3s linear infinite;
          animation: spin3 3s linear infinite;
  -webkit-animation-delay: .3s;
          animation-delay: .3s;
}

.cuatro {
  -webkit-transform-origin: center right;
          transform-origin: center right;
  -webkit-animation: spin4 3s linear infinite;
          animation: spin4 3s linear infinite;
  -webkit-animation-delay: .4s;
          animation-delay: .4s;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  25% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  30% {
    -webkit-transform: rotate(370deg);
            transform: rotate(370deg);
  }
  35% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  25% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  30% {
    -webkit-transform: rotate(370deg);
            transform: rotate(370deg);
  }
  35% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes spin2 {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  20% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  30% {
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
  }
  35% {
    -webkit-transform: rotate(-190deg);
            transform: rotate(-190deg);
  }
  40% {
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
  }
  78% {
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
  }
  95% {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
  98% {
    -webkit-transform: rotate(-370deg);
            transform: rotate(-370deg);
  }
  100% {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}

@keyframes spin2 {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  20% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  30% {
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
  }
  35% {
    -webkit-transform: rotate(-190deg);
            transform: rotate(-190deg);
  }
  40% {
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
  }
  78% {
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
  }
  95% {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
  98% {
    -webkit-transform: rotate(-370deg);
            transform: rotate(-370deg);
  }
  100% {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}

@-webkit-keyframes spin3 {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  27% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);  
