.cinemas {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 20px;
  background-color: wheat;
  border-radius: 20px;
  max-width: 900px;
  margin: 40px auto;
}

.cinemas > h3 {
  width: 100%;
  text-align: center;
  margin-top: 0;
}

.movies {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}
.showtimes {
  margin: 20px auto;
}

.movie-page {
  max-width: 1200px;
  margin: auto;
}

.movie-about {
  margin: 20px auto;
  display: flex;
  flex-direction: row;

}

.movie-about > img {
  max-width: 300px;
  height: 450px;
}

.movie-info {
  margin-left: 20px;
}

.movie-page-rating {
  display: flex;
  flex-direction: row;
}
.movie-page-rating > p {
  margin: auto 0;
}

.youtubevideowrap {
  width: 94%;
  max-width: 640px;
  background: #d1cccc
}

.video-container {
  position: relative;
  padding-bottom: 56.25%;
  padding-top: 30px;
  height: 0;
  overflow: hidden;
}

iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

@media (max-width: 599px) {
  .movie-page {
    text-align: center;
  }

  .movie-about {
    flex-direction: column;
  }

  .movie-about > img {
    margin: auto;
  }

  .movie-page-rating {
    justify-content: center;
  }

  .youtubevideowrap {
    margin: auto;
  }
}

.movie-notfound {
  text-align: center;
}
.movies {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.main__content {
  margin: 20px;
}

.footer {
  text-align: center;
}

